.App {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #f8f9fa;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App-main-no-navbar {
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  flex: 1;
  width: 100%;
}

.search-container-no-navbar {
  text-align: left;
  width: 100%;
}

.search-input {
  width: 100%;
  padding: 12px 16px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-bottom: 20px;
  box-sizing: border-box;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.search-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2);
}

.cards-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.simple-card {
  background: white;
  border-radius: 6px;
  padding: 15px 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  border: 1px solid #e1e4e8;
  cursor: pointer;
}

.simple-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.simple-card-title {
  font-size: 1.1em;
  margin: 0;
  color: #24292e;
  font-weight: 500;
}

.no-results {
  text-align: center;
  padding: 30px 20px;
  font-size: 1.1em;
  color: #666;
}

/* WinBox content styling */
.winbox-content {
  padding: 20px;
  height: calc(100% - 40px);
  overflow-y: auto;
  position: relative;
}

.winbox-content h3 {
  margin-top: 0;
}

.winbox-footer {
  position: absolute;
  bottom: 10px;
  left: 20px;
  right: 20px;
  text-align: center;
  font-size: 0.8em;
  opacity: 0.7;
}

/* Highlighted text styling */
mark {
  background-color: #ffeb3b;
  color: #000;
  padding: 0 2px;
  border-radius: 2px;
  font-weight: 600;
}

.App-footer {
  background-color: #f6f8fa;
  padding: 15px 20px;
  border-top: 1px solid #e1e4e8;
  margin-top: auto;
}

.App-footer p {
  margin: 0;
  color: #666;
  font-size: 14px;
}

.App-footer code {
  background-color: #f1f3f4;
  padding: 2px 6px;
  border-radius: 3px;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  color: #d73a49;
}

/* Responsive design */
@media (max-width: 768px) {
  .App-main-no-navbar {
    padding: 15px;
  }

  .controls-bar {
    flex-direction: column;
    align-items: stretch;
  }

  .window-controls {
    flex-direction: column;
    align-items: stretch;
  }

  .close-all-btn {
    align-self: flex-end;
  }

  .simple-card {
    padding: 12px 16px;
  }

  .simple-card-title {
    font-size: 1em;
  }
}